/* eslint-disable */
<template lang="html">
  <el-row class="main">
    <el-card class="login__box">
      <div slot="header" class="login__header">
        <span> <i class="fa fa-sign-in"></i>  </span>
      </div>
      <el-form :model="formLabelAlign">
        <el-form-item>
          <el-input v-model="formLabelAlign.name" prefix-icon="fa fa-user fa-lg" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-input type="password" v-model="formLabelAlign.region"  prefix-icon="fa fa-lock fa-lg" placeholder=""></el-input>
          <el-alert v-model="formLabelAlign.alert" v-if="formLabelAlign.alert!==''" type="warning" :closable="false">{{formLabelAlign.alert}}</el-alert>
        </el-form-item>
        <!-- <el-form-item prop="type">
          <el-checkbox label="" v-model="formLabelAlign.type"></el-checkbox>
        </el-form-item> -->
        <el-form-item>
          <el-button type="primary" class="login__button" @click="login('formLabelAlign')"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </el-row>
</template>

<script type="text/javascript">
  import router from '../../router/router'
  import axios from 'axios'
  export default {
    //  name 
    name: 'RangeSlider',
    // 
    extends: {},
    // 
    data () {
      return {
        formLabelAlign: {
          name: '',
          region: '',
          type: false,
          alert:''
        },
      }
    },
    computed: {},
    // 
    components: {},
    //  
    watch: {},
    methods: {
      login (formName) {
        this.$router.push({ path: '/admin/index' })
        if(this.formLabelAlign.name == ''){
          return;
        }
        if(this.formLabelAlign.region == ''){
          this.formLabelAlign.alert = '';
          return;
        }
        this.formLabelAlign.alert = '';
        if(this.formLabelAlign.type === true){
          console.log(this.formLabelAlign.type);
          //3
          this.setCookie(this.formLabelAlign.name, this.formLabelAlign.region, 7); 
        }else{
          console.log("Cookie");
          //Cookie
          this.clearCookie();
        }
        //
          axios.post(API_HOST+'Admin/Login',{
            name:this.formLabelAlign.name,
            password:this.formLabelAlign.region
          })
          .then(function(res){
            console.log(res);
            if(res.status = true){
              localStorage.adminName = this.loginForm.name;
              alert("");
              router.push({ path: '/admin/index' });
            }
          })
          .catch(function(err){
            console.log(err);
            alert("");
          });
      },
        //cookie
      setCookie(c_name, c_region, exdays){
        var exdate = new Date(); //
        exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); //
        //cookie
        window.document.cookie = "userName" + "=" + c_name + ";path=/;expires=" + exdate.toGMTString();
        window.document.cookie = "userPwd" + "=" + c_region + ";path=/;expires=" + exdate.toGMTString();
        console.log(window.document.cookie);
      },
      //cookie
      getCookie: function() {
          if (document.cookie.length > 0) {
              var arr = document.cookie.split('; '); //
              for (var i = 0; i < arr.length; i++) {
                  var arr2 = arr[i].split('='); //
                  //
                  if (arr2[0] == 'userName') {
                      this.formLabelAlign.name = arr2[1]; //
                  } else if (arr2[0] == 'userPwd') {
                      this.formLabelAlign.region = arr2[1];
                  }
                  this.formLabelAlign.type = true;
              }
          }
      },
      //cookie
      clearCookie: function() {
          this.setCookie("", "", -1); //21
      }
    },
    // 
    beforeCreate () {},
    //cookie
    mounted () {
      this.getCookie();
    }
  }
</script>

<style scoped lang="scss">
  .main {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url('../../assets/login_bg.jpg');
    background-size: cover;
    background-position: 50%;
    .login__box {
      position: absolute;
      top: 20%;
      // transform: translateY(-56%);
      // -webkit-transform: translateY(-56%);
      right: 70px;
      width: 350px;
      .login__header {
        font-weight: bold;
        text-align: left;
      }
      .login__button {
        width: 100%;
      }
    }
  }
</style>


